import Vue from 'vue'
import Router from 'vue-router'
import camera from './views/camera.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      redirect: '/camera'
    },
    {
      path: '/camera',
      name: 'camera',
      component: camera
    },
    {
      path: '/cameraArray',
      name: 'cameraArray',
      component: () => import('./views/cameraArray.vue')
    },
    {
      path: '/cameraCinematic',
      name: 'cameraCinematic',
      component: () => import('./views/cameraCinematic.vue')
    },
    {
      path: '/cameraLogarithmicdepthbuffer',
      name: 'cameraLogarithmicdepthbuffer',
      component: () => import('./views/cameraLogarithmicdepthbuffer.vue')
    }
  ]
})
