<template>
  <div id="home">
  </div>
</template>

<script>
import { PerspectiveCamera, Vector4, ArrayCamera, Scene, AmbientLight, DirectionalLight, Mesh, PlaneBufferGeometry, MeshPhongMaterial, CylinderBufferGeometry, WebGLRenderer } from 'three'
export default {
  name: 'camera',
  mounted () {
    let amount = 6
    let windowW = window.innerWidth
    let windowH = window.innerHeight
    let devicePixelRatio = window.devicePixelRatio
    let aspectRatio = windowW / windowH
    let width = windowW / amount * devicePixelRatio
    let height = windowH / amount * devicePixelRatio

    let cameras = []
    for (let y = 0; y < amount; y++) {
      for (let x = 0; x < amount; x++) {
        let subcamera
        subcamera = new PerspectiveCamera(40, aspectRatio, 0.1, 10)
        subcamera.viewport = new Vector4(Math.floor(x * width), Math.floor(y * height), Math.ceil(width), Math.ceil(height))
        subcamera.position.set((x / amount) - 0.5, 0.5 - (y / amount), 1.5)
        subcamera.position.multiplyScalar(2)
        subcamera.lookAt(0, 0, 0)
        subcamera.updateMatrixWorld()
        cameras.push(subcamera)
      }
    }

    let camera = new ArrayCamera(cameras)
    camera.position.z = 3
    let scene = new Scene()

    scene.add(new AmbientLight(0x222244))
    let light = new DirectionalLight()
    light.position.set(0.5, 0.5, 1)
    light.castShadow = true
    light.shadow.camera.zoom = 4
    scene.add(light)

    let background = new Mesh(new PlaneBufferGeometry(100, 100), new MeshPhongMaterial({ color: 0x000066 }))
    background.receiveShadow = true
    background.position.set(0, 0, -1)
    scene.add(background)

    let mesh = new Mesh(new CylinderBufferGeometry(0.5, 0.5, 1, 32), new MeshPhongMaterial({ color: 0xff0000 }))
    mesh.castShadow = true
    mesh.receiveShadow = true
    scene.add(mesh)

    let renderer = new WebGLRenderer()
    renderer.setPixelRatio(devicePixelRatio)
    renderer.setSize(windowW, windowH)
    renderer.shadowMap.enabled = true
    document.getElementById('home').appendChild(renderer.domElement)

    function animate () {
      mesh.rotation.x += 0.05
      mesh.rotation.z += 0.01
      renderer.render(scene, camera)
      requestAnimationFrame(animate)
    }
    animate()
  }
}
</script>
